import React from "react";
import {Container} from "react-materialize";

const NoMatch = () =>
		<div>
		<Container>
          <h1>404 Page Not Found</h1>
          <h1>
            <span role="img" aria-label="Face With Rolling Eyes Emoji">
              🙄
            </span>
          </h1>
         </Container>
          </div>;

export default NoMatch;



